.link {
  color: $colorBlue;
  text-decoration: none;
}

.testimonial {
  &__photo--container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;
    margin-bottom: 28px;
  
    @include onDesktop() {
      margin-bottom: 32px;
    }

    transition: transform 0.5s;

    &:hover {
      background-color: #fff;
      border-radius: 30px;

      cursor: pointer;
      transform: scale(1.2);
      z-index: 1;
    }
  }

  
  
  &__photo {
    width: 100%;
    border-radius: 50%;
    grid-column: 2 / 4;
  }
  
  &__name {
    margin: 0 0 4px;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
  
    text-align: center;
    color: $colorBlue;
  }
  
  &__role {
    margin: 0 0 4px;
    font-weight: normal;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
  }
  
  &__about {
    text-align: center;
  }
}
