.about {
  &__title {
    margin: 0 0 48px;
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
  
    align-items: center;
    letter-spacing: -1.5px;
  
    &--link {
      color: $colorBlue;
      text-decoration: none;
    }
  
    @include onDesktop() {
      margin-bottom: 0;
      font-size: 48px;
      line-height: 48px;
    }
  }
  
  &__subtitle {
    display: block;
    margin-bottom: 16px;
  
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: $colorBlue;
    text-decoration: none;
  }
  
  &__paragraph {
    margin: 0 0 27px;
  
    &:last-child {
      margin-bottom: 0;
    }
  
    &--important {
      font-weight: bold;
    }
  }
}
